import {
    PORTALS
} from './../type'

const  portalsState = {
    portals:[]
}

const portalReducer = (state = portalsState, action) => {
    switch (action.type) {
    case PORTALS:
        return {
            portals: action.payload
        }
    default:
    return state
    }
 } 

 export default portalReducer