import React, { Component } from 'react';
import PortalPresentation from './Portal';
import {connect} from 'react-redux';
import {Redirect} from 'react-router-dom'
import {LoadUser , loadPortals} from './../../../../action/auth';


class Portals extends Component {
    componentDidMount(){
        this.props.loadPortals();
    }
    render() {
        
        if (!this.props.auth) return <Redirect to={'/login'} />
        return (
            <div>
                <PortalPresentation portals={this.props.portals} {...this.props.userInfo}/>
            </div>
        )
    }
}


const mapStateToProps  = state=>{
    let isValid = state.auth.log === {} ? null : state.auth.log.success;
 
    return {
        auth: isValid,
        userInfo: isValid ? state.auth.log.response : null,
        portals: state.portalReducer.portals
    }
}

export default connect(mapStateToProps,{LoadUser,loadPortals})(Portals)