import React from 'react'
import './style.css'
import LinkIcon from '../../compnent/LinkIcon/LinkIcon'
import logo from './../../../img/logo.jpg'
import { Link } from 'react-router-dom'

 
const SideLink = (props)=>{
        return (
            <div className='w-100 sidebar-link-container'>
                <div className ='col-12' style={{height:'15vh',display:'flex',justifyContent:'center',alignItems:'center'}}>
                    <Link to='/dashboard/' className='dashboard-logo-img pt-5 h3'><img className='navbar-logo' src={logo} alt='fasthelpers logo' /></Link>
                </div>
                <div className='mx-auto pt-5'>
                    <LinkIcon url={'/dashboard/profile'} link={'PERSONAL INFO'} icon={'fa fa-user'}/>
                    <LinkIcon url={'/dashboard/account-update'} link={'ACCOUNT INFO'} icon={'fa fa-home'}/>
                    <LinkIcon url={'/dashboard/nextofkin'} link={'NEXT OF KIN INFO'} icon={'fa fa-home'}/>
                    <LinkIcon url={'/dashboard/portals'} link={'PORTALS'} icon={'fa fa-home'} />
                    <LinkIcon url={'/dashboard/reset-password'} link={'PASSWORD RESET'} icon={'fa fa-home'} />
                </div>
            </div>
        )
    }

export default SideLink