import React from 'react';

export const Card = props => {
    let {text, children} = props
    return(
        <div className='mycard-container w-100 mt-4'>
            <div className='text-white bg-primary mycard-header py-4 px-3 d-flex'>
                <p className='m-auto'>{text}</p>
            </div>
            <div className='mycard-body bg-white py-4 px-3'>
                {children}
            </div>
        </div>
    )
} 

export const CardForPortal = props => {
    let { text, children, recycle} = props
    return (
        <div className='mycard-container w-100 mt-4'>
            <div className='text-white bg-primary mycard-header py-4 px-3 d-flex position-relative'>
                <p className='m-auto'>{text}</p>
                    <span className='recycle-portal d-block text-center small'><span className='text-dark'></span> <br/><span className='text-primary'>{recycle === 0 ? 1 : recycle}</span></span>
                
                    </div>
            <div className='mycard-body bg-white py-4 px-3'>
                {children}
            </div>
        </div>
    )
} 


export const InputWithLable = props =>{
    let {
        label,
        borderClass,
        name,
        value,
        notEdit,
        handleChange,
        placeholder
    } = props
    return(
        <div className='row w-100 mt-3'>
            <p className='bold col-md-4 col-12 text-md-right my-auto my-auto'>{label}</p>
            <input value={value}  
            onChange={handleChange} 
            disabled={notEdit} 
            type='text' 
            placeholder={placeholder} 
            name={name} 
            className={`${borderClass} py-2 input-with-label col-12 col-md-8`}/>
        </div>
    )
}
export const InputWithLablePassword = props =>{
    let {
        label,
        borderClass,
        name,
        value,
        notEdit,
        handleChange,
        placeholder,
    } = props
    return(
        <div className='row w-100 mt-3'>
            <p className='bold col-md-4 col-12 text-md-right my-auto my-auto'>{label}</p>
            <input value={value}  
            onChange={handleChange} 
            disabled={notEdit} 
            type={notEdit ?'password': 'text'} 
            placeholder={placeholder} 
            name={name} 
            className={`${borderClass} py-2 input-with-label col-12 col-md-8`}/>
        </div>
    )
}

export const InputDisable = props => {
    let {
        label,
        name,
        value,
        handleChange,
        placeholder
    } = props
    return (
        <div className='row w-100 mt-3'>
            <p className='bold col-md-4 col-12 text-md-right my-auto my-auto'>{label}</p>
            <input value={value}
                onChange={handleChange}
                disabled={true}
                type='text'
                placeholder={placeholder}
                name={name}
                className={`myinput-border-none py-2 input-with-label col-12 col-md-8`} />
        </div>
    )
}

export const InputDropDown = props => {
    let {
        label,
        name,
        value,
        notEdit,
        handleChange,
    } = props
    return (
        <div className='row w-100 mt-3'>
            <p className='bold col-md-4 col-12 text-md-right my-auto my-auto'>{label}</p>
            
            {
                !notEdit?
                <select
                onChange={handleChange}
                name={name}
                className={`myinput-border-none py-2 input-with-label col-12 col-md-8`} >
                    {props.children}
            </select>:<p>{value}</p>

            }
        </div>
    )
}

export const VerifKey = (props)=>{
    return(
        <div className = 'position-adsolute d-flex w-100  overlay-uniquekey' >
            <form onSubmit={props.handleSubmitKey} className='m-auto'>
                <p className='text-center text-danger'>{props.msg} </p>
                <input onChange={props.handleChange} name='uniqueKey' value={props.uniqueKey} className='form-control my-4' placeholder='Enter Unique key' type='password'/>
                <button onClick={props.handleSubmitKey} className='btn btn-primary text-white px-5'>Submit</button>
                <span className='close-unique' onClick={props.handleToggleEditCancel}>x</span>
            </form>
        </div>
    )
}