import React from 'react'
import { CardForPortal } from '../Template'

const PortalPresentation = (props) => {  
    let {portal , level,portals , userName} = props
        return (
            <div>
             <div className='row' >
                {
                    portals.map((portal , index)=>{
                        return (
                            <div key={index} className='col-12 col-md-4'>
                                <CardForPortal text={`Portal ${portal.portal}`} recycle={portal.level% 3}>
                                    <p className='mb-0 bold'>User Name: <span className='text-secondary'>{portal.userName}</span></p>
                                    <p className='mb-0 text-success bold'>Recycle Account</p>
                                    <p className='mb-0 bold'>Status: <span className={portal.portal > 8 ? "text-danger":"text-success"}>{portal.portal > 8 ? "Inactive":"Active"}</span></p>
                                    <p className='mb-0 bold'> <span className={portal.level % 3 === 0 ? "text-danger":portal.level % 3 === 2? 'text-danger': "text-success"}>{portal.level % 3 === 0 ? "PENDING":portal.level % 3 === 2? 'PENDING': "COMPLETED"}</span></p>

                                </CardForPortal>
                            </div>
                        )
                    })
                }
                    <div className='col-12 col-md-4'>
                        <CardForPortal text={`Portal ${portal}`} recycle={level% 3}>
                            <p className='mb-0 bold'>User Name: <span className='text-secondary'>{userName}</span></p>
                            <p className='mb-0 text-success bold'>Main Account</p>
                            <p className='mb-0 bold'>Status: <span className={portal > 8 ? "text-danger":"text-success"}>{portal > 8 ? "Inactive":"Active"}</span></p>
                            <p className='mb-0 bold'> <span className={level % 3 === 0 ? "text-danger":level % 3 === 2? 'text-danger': "text-success"}>{level % 3 === 0 ? "PENDING":level % 3 === 2? 'PENDING': "COMPLETED"}</span></p>
                        
                        </CardForPortal>
                    </div>
                </div>   
            </div>
        )
    }


export default PortalPresentation