import React, { Component } from 'react'
import { Paragraph, AboutUsHeader,TextHeader } from '../../Template'
import {Link} from 'react-router-dom'


export default class Ethics extends Component {
    render() {
        return (
            <div>
            <section className='pt-5'>
                    <AboutUsHeader text={'Our Ethics '} />
                    <hr className='mt-0'/>
                    <TextHeader text='INTRODUCTION BACKGROUND'/>
                    <Paragraph isAboutUs={true} text='This is an association of likeminded people. It seek to have a common strong force, share union/fellowship, providing training/capacity building, Raising Financial support-through Mutual Electronic Funding (MEFunding), etc, raising entrepreneurs and making life meaningful for all'/>
                    <section className='mb-5'>
                        <TextHeader text='OBJECTIVES'/>
                        <ul>
                            <li>Helping the members to do and be thier best in this Global World</li>
                            <li>To help members live and enjoy thier full life  as God has blessed and ordain us to live</li>
                            <li>To raise entrepreneurs and life changers to transform our communities, country, Africa and the world</li>
                            <li>To make member faithful, be responsible and active in their field of work</li>
                        </ul>

                    </section>
                      <section className='mb-5'>
                        <TextHeader text='GUIDING PRINCIPLES'/>
                        <Paragraph isAboutUs={true} text='The guiding principles of the Association/Club in policy/constitution are base on transparency, truth as of the Bible standard (give and it shall be given unto you...,), Business Principles (first come, first served), Caring and Love for all members.'/>
                    </section>
                    <section className='mb-5'>
                    <TextHeader text='EQUITY'/>
                        <Paragraph isAboutUs={true} text='Being a member of the Group, the club shall at all times recognize and preserve the rights, responsibilities and equality of all members regardless of country, tribe, sex, color, religion, political and social status. we are all created by God.'/>
                    </section>
                     <section className='mb-5'>
                    <TextHeader text='POLICY STATEMENT'/>
                        <Paragraph isAboutUs={true} text='FHGG IS FOCUSED TO SEE OUR MEMBERS TRAINED, EQUIPED AND RAISED IN LIFE TO SUPPORT OTHERS TO GROLIFY OUR MAKER. " WHETHER THEREFORE YE EAT, OR DRINK,  OR WHATSEVER YE DO, DO ALL TO THE GLORIFICATION OF GOD " ( 1 COR 10:31 )'/>
                    </section>
                    <AboutUsHeader text={'MEMBERSHIP '} />

                    <Paragraph isAboutUs={true} 
                    text = {
                        'Memberhip is subjected to desire, registration and willingness to contribute to the growth of the Association/Club.'
                    }
                    />
                    <ul>
                        <li>Must be a matual sound minded person</li>
                        <li>Must be above 16years</li>
                        <li>Must be a registered member</li>
                        <li>Must contribute to the growth of the Association by</li>
                        <li className='list-style-type-none'>
                            <lo>
                                <li>- Spreading the news about us to others on social media</li>
                                <li>- By word of mouth</li>
                                <li>- Group presentation</li>
                                <li>- Directing others to the office</li>
                            </lo>
                        </li>
                        <li className='bold '>This is an Association of Associations ( Fun clubs, Fellowships, Businessess, etc. can also register )</li>
                        <li>Details for registration for a member are as follows:</li>
                        <li className='list-style-type-none'>
                            <ol>
                                <li>Name:</li>
                                <li>Contact number (Should be a registered Mobile money number )</li>
                                <li>Email address ( It can be created for you if you do not have one, write create for me with your data of birth) </li>
                                <li>User name</li>
                                <li>Town</li>
                                <li>Region</li>
                                <li>country</li>
                                <li>Referrer</li>
                                <li>Bank account ( can be provided later ) </li>
                            </ol>
                        </li>
                    </ul>
                </section>
                <section className='pt-3'>
                    <TextHeader text={'WHO IS ELIGIBLE TO JOIN THIS ASSOCIATION ( CLUB )?'}/> 
                    <hr/> 
                    <p>
                        <ol className='join-us-list'>
                            <li>Business Owners</li>
                            <li>Students</li>
                            <li>Traders </li>
                            <li>Self employed people</li>
                            <li>Unemployed people [educated or uneducated]</li>
                            <li>Pastors [Ministries/Fellowships]</li>
                            <li>Anyone looking for a genuine opportunity to transform his/her life.</li>
                        </ol>
                            <p className='h4'>(Open to all but TERMS AND CONDITIONS APPLIES)</p><br/>
                        <Link to='/signup' className='btn btn-primary'>Register Now</Link>
                    </p>

                </section>
            </div>
        )
    }
}
