import React, { Component } from 'react'
import Signup from './Signup'
import Account from './Account';
import {Route} from 'react-router-dom'
import NextOfKings from './NextOfKings';

import { newMember, LoadUser, registerNextOfKing} from './../../../action/auth';
import {connect} from 'react-redux'

import countries from './../../country';

const emailRegx = RegExp(/^[A-z0-9.!#$%&'*+/=?^_`{|}~-]+@[A-z0-9-]+(?:\.[A-z0-9-]+)*$/);
const phoneRegx = RegExp(/[0-9/s]/);

class SignupContainer extends Component {
    componentWillMount(){
        if(!this.props.auth){
            this.props.LoadUser()
        }
    }
    constructor(){
        super();
        this.state = {
            firstName:"",
            lastName:"",
            nFirstName:"",
            nLastName:"",
            nAddress:'',
            nEmail:'',
            nDay:'',
            nMonth:'',
            nYear: '' ,
            relationship:'',
            town:'',
            region:'',
            country:'',
            password:'',
            comfirmPassword:'',
            email:'',
            number:'',
            nNumber:'',
            userName:'',
            uniqueKey:'',
            errorMessage:{
                firstName:'',
                lastName:'',
                town:'',
                region:'',
                country:'',
                email:'',
                number:'',
                nNumber:'',
                password:'',
                nFirstName:"",
                nLastName:"",
                nAddress:'',
                nEmail:'',
                nDay:'',
                nMonth:'',
                nYear: '' ,
                relationship:'',
                userName:'',
                uniqueKey: '',
            }
        }
        this.handleChange = this.handleChange.bind(this);
        this.handleToNextOfKing = this.handleToNextOfKing.bind(this);
        this.handleBackward = this.handleBackward.bind(this);
        this.handleToAccount = this.handleToAccount.bind(this);
        this.handleBackwardToSignup = this.handleBackwardToSignup.bind(this);
        this.handleSkip = this.handleSkip.bind(this);
    }
    handleSkip(e) {
        e.preventDefault();
        return this.props.registerNextOfKing({ ownerId: this.props.userInfo._id})  

    }
    handleChange(e){
        e.preventDefault()
        let {name ,value} = e.target
        let {errorMessage} = this.state

        switch (name) {
            case 'firstName':
                // MORE VARIFICATION
                    value = value.replace(/\s{2}/,' ');
                    value = value[0] === ' ' ? value.replace(' ', '') : value;
                    errorMessage.firstName = value.length < 2 && value.length > 0  ? 'invalid input at least three(3) letter' : '';
                break;
            case 'lastName':
                // MORE VARIFICATION     
                    value = value.replace(/\s{2}/, ' ');
                    value = value[0] === ' ' ? value.replace(' ', '') : value;
                    errorMessage.lastName = value.length < 2 && value.length > 0 ? 'invalid input at least three(3) letter' : '';
                break
            case 'town':
                // MORE VARIFICATION 
                    value = value.replace(/\s{2}/, ' ');
                    value = value[0] === ' ' ? value.replace(' ', '') : value;
                    errorMessage.town = value.length < 2 && value.length > 0 ? 'invalid input at least three(3) letter': '';
                break
            case 'region':
                // MORE VARIFICATION                
                    value = value.replace(/\s{2}/, ' ');
                    value = value[0] === ' ' ? value.replace(' ', ''): value;
                    errorMessage.region = value.length < 2 && value.length > 0 ? 'invalid input at least three(3) letter' : '';
                break
            case 'country':
                //DONE
                errorMessage.country =  value !== 'country' ? '':'select country';
                break
            case 'email':
                // DONE                              
                value = value.replace(' ', '')
                errorMessage.email =  !emailRegx.test(value) && value.length > 0 ?'invalid email' : '';
                break
            case 'number':
                // DONE
                value = value.replace(/[\s{2}A-Za-z]/, '');
                value = value.replace(' ', '')
                errorMessage.number = !phoneRegx.test(value) && value.length > 0 ?'invalid input, numbers only': '';
                break
            case 'password':
                // MORE VARIFICATION
                value = value.replace(' ', '')
                errorMessage.password = value.length < 5 && value.length > 0 ? 'invalid password input at least six(6) letters ':''
                break
            case 'comfirmPassword':
                // MORE VARIFICATION
                    value = value.replace(' ', '')
                break
            case 'userName':
                // MORE VARIFICATION
                value = value.replace(/\s{2}/,' ');
                value = value[0] === ' ' ? value.replace(' ', '') : value;
                errorMessage.userName = value.length < 2 && value.length > 0 ? 'invalid input at least three(3) letters' : ''
                break;
            case 'nFirstName':
                value = value.replace(/\s{2}/, ' ');
                value = value[0] === ' ' ? value.replace(' ', '') : value;
                errorMessage.nFirstName = value.length < 2 && value.length > 0 ? 'invalid input at least three(3) letter' : '';
                break;
            case 'nLastName':
                value = value.replace(/\s{2}/, ' ');
                value = value[0] === ' ' ? value.replace(' ', '') : value;
                errorMessage.nLastName = value.length < 2 && value.length > 0 ? 'invalid input at least three(3) letter' : '';
                break;
            case 'nAddress':
                value = value.replace(/\s{2}/, ' ');
                value = value[0] === ' ' ? value.replace(' ', '') : value;
                errorMessage.nAddress = value.length < 2 && value.length > 0 ? 'invalid input at least three(3) letter' : '';
                break;
            case 'nNumber':
                value = value.replace(/[\s{2}A-Za-z]/, '');
                value = value.replace(' ', '')
                errorMessage.nNumber = !phoneRegx.test(value) && value.length > 0 ?'invalid input, numbers only': '';
                break;
            case 'nMonth':
                value = value.replace(/[\s{2}A-Za-z]/, '');
                value = value[0] === ' ' ? value.replace(' ', '') : value;
                errorMessage.nMonth = value.length < 2 && value.length > 0 ? 'Month  in number' : '';
                break;
            case 'nDay':
                value = value.replace(/[\s{2}A-Za-z]/, '');
                value = value[0] === ' ' ? value.replace(' ', '') : value;
                errorMessage.nDay = value.length < 2 && value.length > 0 ? 'Day in number' : '';
                break;
            case 'nYear':
                value = value.replace(/[\s{2}A-Za-z]/, '');
                value = value[0] === ' ' ? value.replace(' ', '') : value;
                errorMessage.nYear = value.length < 2 && value.length > 0 ? 'Year in number' : '';
                break;
            case 'relationship':
                errorMessage.relationship = value !== 'null' ? '' : 'select relationship';
                break
            case 'nEmail':
            value = value.replace(' ', '')
            errorMessage.nEmail = !emailRegx.test(value) && value.length > 0 ? 'invalid email' : '';
                break;
            case 'uniqueKey':
                console.log(value);
                
                value = value.replace(' ', '');
                errorMessage.uniqueKey = value.length > 0 && value.length < 4 ? 'invalid input at least four(4) letter' : '';
                break;
            default:
                break;
        }
        this.setState({[name]:value,errorMessage})
    }
validateError = (obj)=>{
    let isValid = true;
    let {errorMessage} = this.state
    Object.values(errorMessage).forEach(value=>{
        value.length > 0 && (isValid = false) 
    });
    Object.values(obj).forEach(value=>{
        value === '' && (isValid = false) 
    });
    return isValid
}


    handleToAccount(e){
        e.preventDefault()   
        let {nFirstName,nLastName,nAddress,nNumber,nMonth,nDay,nYear,relationship,nEmail} = this.state ;
        let credencial = {
            relationship,
            firstName: nFirstName,
            lastName: nLastName,
            address: nAddress,
            number: nNumber,
            month: nMonth,
            year: nYear,
            email: nEmail,
            day: nDay,
            ownerId: this.props.userInfo._id
        }
        if(this.validateError(nFirstName,nLastName,nAddress,nNumber,nMonth,nDay,nYear,relationship,nEmail)){
            return this.props.registerNextOfKing(credencial)  
        }else{
            return this.setState({msg: 'ERROR => Please Complete the forms with a valid input.'})
        }    
            // this.props.history.push('/signup/account')
    }



    handleToDashBoard(e) {
        e.preventDefault()
        let { nFirstName, nLastName, nAddress, nNumber, nMonth, nDay, nYear, relationship, nEmail } = this.state;
        let credencial = {
            relationship,
            firstName: nFirstName,
            lastName: nLastName,
            address: nAddress,
            number: nNumber,
            month: nMonth,
            year: nYear,
            email: nEmail,
            day: nDay,
            ownerId: this.props.userInfo._id
        }
        if (this.validateError(nFirstName, nLastName, nAddress, nNumber, nMonth, nDay, nYear, relationship, nEmail)) {

            return this.props.registerNextOfKing(credencial)
        } else {
            return this.setState({ msg: 'ERROR => Please Complete the forms with a valid input.' })
        }
        
    }


    handleToNextOfKing(e){
        e.preventDefault();
        
        let {password ,comfirmPassword ,firstName ,lastName ,town ,region ,country ,number ,email ,userName , uniqueKey} = this.state ;
        let credencial = {
            firstName,
            lastName,
            town,
            region,
            country,
            number,
            email,
            userName: userName.toLocaleLowerCase(),
            password,
            uniqueKey
        }
        if(password !== comfirmPassword){
            return this.setState({msg:`ERROR => Password don't match. ${password} !== ${comfirmPassword}`})
        }
        if(this.validateError({firstName,lastName,town,region,country,number,email,userName,uniqueKey}) && password === comfirmPassword){
            return this.props.newMember(credencial)  
        }else{
            return this.setState({msg: 'ERROR => Please Complete the forms with a valid input.'})
        }
    }

    handleBackward(e){
        e.preventDefault()
        this.props.history.goBack();
    }

    handleBackwardToSignup(e){
        e.preventDefault();
        this.props.history.push('/signup')
    }
    render() {
        
        return (
            <div>
                <Route path='/signup' exact render= {props=><Signup countries={countries.countryList} {...this.props} {...this.state} handleChange={this.handleChange} handleSubmit={this.handleToNextOfKing} />}/>
                <Route path='/signup/nextofking' exact render={props => <NextOfKings {...this.props} {...this.state} handleSkip={this.handleSkip} handleBackwardToSignup={this.handleBackwardToSignup} handleChange={this.handleChange} handleSubmit={this.handleToAccount} />}/>
                <Route path='/signup/account' exact render={props=><Account  handleBackward={this.handleBackward}/>}/>
            </div>
        )
    }
}


const mapStateToProps = (state)=>{
    let isValid = state.auth.log === {} ? {} : state.auth.log.success 
    return{
        auth: isValid,
        updateError: Object.keys(state.errorReducer).length > 0 ? state.errorReducer: {},
        userInfo: isValid ? state.auth.log.response: {} ,
        message: state.auth.message
    }
}

export default connect(mapStateToProps , {newMember , LoadUser , registerNextOfKing})(SignupContainer)