import React, { Component } from 'react'
import './style.css';
import {
    Paragraph,
    AboutUsHeader
} from '../../Template';

export default class AboutUs extends Component {
    render() {
        return (
            <div className='pb-5 pt-3'>
                <section>
                    <AboutUsHeader text={'FastHelpers Group-Ghana (A Global Association )'}/>
                    
                    <Paragraph isAboutUs={true} text={'FASTHELPERS GROUP-GHANA [FHGG]  is a global assciation with life empowerment and training programs, financial networking system, health care and a welfare system which has been created to help members acquire skills, capacity building, financial freedom, good health care etc.'}/>
                    
                    <Paragraph isAboutUs={true} text={'We believe in creating business opportunities, life empowerment etc, by coaching, raising and empowering young men and women exspecially to become Entrepreneurs or independent using the power of training and networking.'}/>
                    
                    <Paragraph isAboutUs={true} text={'It is out of this vision that FASTHELPERS GROUP-GHANA [FHGG]  has been born to help people from all walks of life, use this assciation/system as a springboard to acquire necessary knowledge and skills to become self reliance and while the association helps raise the needed capital to pursue their dreams and goals.'}/>
                    
                </section> 
                <section>
                    <AboutUsHeader text={'Our Mission'} />
                    <Paragraph isAboutUs={true} text={'Raising ordinary people to become extra-ordinary empowered life changers i.e through training, financial literacy and the creation of wealth with the aid of a credible and trusted system, and working together as one with a common goal.'} />
                    <AboutUsHeader text={'Our Vision'} />
                    <Paragraph isAboutUs={true} text={'To become a leading association in Ghana and beyond, transforming lifes, ensuring financial independence of its members in the near future, relying on committed leaders, motivated members and a trusted system.'} />

                </section>
                <section className='pt-5'>
                    <AboutUsHeader text={'Join us'}/>
                   <Paragraph isAboutUs={true} text={'FASTHELPERS GROUP-GHANA ( A GLOBAL ASSOCIATION ) has programs that gives immediate result and offers learning, health care and earning potentials.'} />
                    <Paragraph isAboutUs={true} text={'FASTHELPERS GROUP-GHANA ( FHGG ) has available materials, resouce personals and training programmes that helps its members to acquire entrepreneurial knowledge, life empowerment programs and other skills that help them to achieve success in the world of entrepreneurship/Business and other professions.'} />
                    <Paragraph isAboutUs={true} text={'You can work from anywhere irrespective of where you are and what you do, members are given great training and team support in order for them to achieve success in their business and other fields.'} />
                    <Paragraph isAboutUs={true} text={'By the training and support given you, you will be able to achieve great success leading to a great life, better health, financial freedom etc. the Associate makes income for every enrollment they make from portal to portal.'} />
                    <Paragraph isAboutUs={true} text={'We invite you to join us today, to become part of a bigger, likeminded community of achievers with an entrepreneurial and life transforming spirit,while raise capital to fulfill our entrepreneurial dreams and goals, giving back to society in the process.'} />
                </section>
                
            </div>
        )
    }
}
