import React, { Component } from 'react';
import {Card} from '../Template';
import { connect } from 'react-redux';
import {Redirect} from 'react-router-dom'



// <<<<<<<<<<<<<< CHANGES MADE >>>>>>>>>>>>>>>
//  THIS NOW DISPLAYS UNRECIEVED BENEFIT
class PayedBenefit extends Component {
    render() {
        if(!this.props.auth) return <Redirect to={'/login'}/>

        return (
            <div>
                <Card text={'Unreceived Benefits'} >
                    <div className='position-relative'>

                        {this.props.payment.map((data, index)=>{
                            return !data.paid? (<p key={index} className='small text-secondary bold mb-1 bg-white' >
                                <span className='large text-black bold'><i className='fa fa-envolop'></i>Portal:</span> {data.portal} 
                                <span className='text-black bold'> Level:</span> {data.level === 3 ? 2: data.level}   payment {} <br/> 
                                <span className='text-success bold'> Amount:</span> {data.amount} </p>):''
                        })}
                    </div>
                </Card>
            </div>
        )
    }
}

const mapStateToProps = state=>{
    let isValid = state.auth.log === {} ? null : state.auth.log.success;
    return{
        auth: isValid,
        userInfo: isValid ? state.auth.log.response : null,
        payment: state.paymentReducer.payment
    }
}
export default connect(mapStateToProps,{})(PayedBenefit)