import React, { Component } from 'react'
import Slider from "react-slick";


import image from './../../../../img/f1.jpg'
import image2 from './../../../../img/f2.jpg'
import image3 from './../../../../img/f3.jpg'
import image4 from './../../../../img/f4.jpg'
import { Paragraph, HeaderLink } from '../../../Template';
import Link from 'react-router-dom/Link';

export default class LargeSideContainer extends Component {
    render() {
    let settings = {
      dots: true,
      infinite: true,
      speed: 300,
      slidesToShow: 1,
      slidesToScroll: 1,
      vertical: false,
      autoplay:true,
      arrows:true,
     
    };
        return (
            <div>
                <section className='w-100 large-side-container'>
                    <Slider {...settings}>
                        <div className='homepage-header-img'>
                            <img src={image} alt='company name' className='w-100'/>
                        </div>
                        <div  className='homepage-header-img'>
                            <img src={image2} alt='company name' className='w-100'/>
                        </div>
                        <div  className='homepage-header-img'>
                            <img src={image3} alt='company name' className='w-100'/>
                        </div>
                        <div  className='homepage-header-img'>
                            <img src={image4} alt='company name' className='w-100'/>
                        </div>
                    </Slider>
                </section>
                <section className='pt-5'>
                    
                    {/* Welcome to Fast Helpers */}
                    <HeaderLink url={'/about-us'} text={'Welcome to FASTHELPERS GROUP [ASSOCIATION] GLOBAL'}/>
                    <p className='about-description-text'>Are you still wondering how to get a reliable Family Grouping or Association that brings you 
                        <ul style={{fontWeight:'bold',borderBottom:'0px'}}>
                            <li>Free Trainings</li>
                            <li>Mntorship </li>
                            <li>Raise Funds to kick start your dream Project.</li>
                            <li>Further your Education.</li>
                            <li>Live your dream life. </li>
                            <li>Cater for that Essential need? </li>
                        </ul>
                        You need not worry anymore.
                    </p>
                    <p className='about-description-text'>
                        <strong>FASTHELPERS GROUP [ASSOCIATION] GLOBAL</strong> 
                        is here and, we bring to your doorsteps these opportunities to live the dream life or, the comfort you have ever envisaged to live.
                    </p>
                    <p className='about-description-text'>
                        <strong>(one time only  non refundable membership registration fee)</strong> to launch you into a realm of endless <strong> Capacity Building, Mentorship Program Wealth Making Activities etc. 
                        YES, just a onetime payment of GH110 will change your life for good as the Lord leads.</strong>
                    </p>
                    <p className='about-description-text'>
                        With us, you dont need any Educational qualification to be a Member of this Life Changing Association(it is open to all levels of Society). All you need is your 
                        <strong>Dedication, Commitment </strong>
                        and you will by HIS Grace arrive at your dream destination in no time with us.<br/>
                        <Link to='/howitworks'>Read more</Link>
                    </p>
                    <Link to='/signup' className='d-block mb-5 btn btn-primary px-5'>Register Now</Link>

                    {/* What is Fast Helpers */}
                    <HeaderLink url={'/about-us'} text={'What is FASTHELPERS GROUP [ASSOCIATION] GLOBAL'} />
                    <Paragraph readMore={true} url={'/about-us'} text={'FASTHELPERS GROUP [ASSOCIATION] GLOBAL is a Financial Networking System, Health Care and Training group which has been created to help people acquire financial freedom and good health care and to become entrepreneurs, helping them to raise capital to start their own businesses through the power of networking.'} />
                    
                    {/* Mission */}
                    <HeaderLink url={'/about-us'} text={'Our Mission'}/>
                    <Paragraph text={'Raising ordinary people to become financially empowered through financial literacy and the creation of wealth with the aid of a credible and trusted system, and working together as one with a common goal.'}/>

                    {/* Vision */}
                    <HeaderLink url={'/about-us'} text={'Our Vision'} />
                    <Paragraph text={'To become a leading association in Ghana and beyond, ensuring financial independence of its members in the near future, and relying on committed leaders, motivated members and a trusted system.'} />
                </section>
            </div>
        )
    }
}
