import {
    KEY_INVALIDE,
    KEY_VALIDE
} from './../type'

const  portalsState = {
    key:false,
    message:''
}

const uniqueReducer = (state = portalsState, action) => {
    switch (action.type) {
    case KEY_VALIDE:
        return {
            key: action.payload,
            message: ''
        }
    case KEY_INVALIDE:
        return {
            key: action.payload,
            message:'Please try again in a few minite',
        }
    default:
    return state
    }
 } 

 export default uniqueReducer