import {
    LOGIN_FAILED,
    LOGIN_SUCCESSFUL,
    SIGNUP_SUCCESSFUL,
    SIGNUP_FAILED,
    PROFILE_UPDATE
} from '../type.js'


let user = {
    user: {}
};

const successMessage = 'AUTHORIZE'
const failedMessage = 'UNAUTHORIZE'



export default function userInfo(state=user,action){

    switch (action.type) {
        case LOGIN_SUCCESSFUL:
            return {
                message: successMessage,
                user:action.payload
            }
        case LOGIN_FAILED:
            return {
                message: failedMessage
            }
        case SIGNUP_SUCCESSFUL:
            return{
                message: successMessage
            }
        case SIGNUP_FAILED:
            return{
                message: action.payload.message
            }
        case PROFILE_UPDATE:
            return {
                message:'Update Successfull',
                user: {...state.user , ...action}
            }
        default:
            return state
    }
}